'use client';

// Force rebuild to clear cache
import { useState } from 'react';
import { useQuery } from '@tanstack/react-query';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { visitsApi, nursesApi, patientsApi } from '@/services/api-client';
import { formatDate, formatTime, getInitials } from '@/lib/utils';
import { 
  Calendar, 
  Plus, 
  ChevronLeft, 
  ChevronRight, 
  Clock,
  User,
  MapPin,
  Eye,
  Edit,
  Trash2
} from 'lucide-react';

export default function SchedulePage() {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [selectedVisit, setSelectedVisit] = useState<any>(null);

  const { data: visits, isLoading: visitsLoading } = useQuery({
    queryKey: ['visits'],
    queryFn: () => visitsApi.getAll(),
  });

  const { data: nurses } = useQuery({
    queryKey: ['nurses'],
    queryFn: () => nursesApi.getAll(),
  });

  const { data: patients } = useQuery({
    queryKey: ['patients'],
    queryFn: () => patientsApi.getAll(),
  });

  const getNurseName = (nurseId: string) => {
    return nurses?.find(n => n.id === nurseId)?.name || 'Unknown Nurse';
  };

  const getPatientName = (patientId: string) => {
    return patients?.find(p => p.id === patientId)?.name || 'Unknown Patient';
  };

  const getNurseInitials = (nurseId: string) => {
    const nurse = nurses?.find(n => n.id === nurseId);
    return nurse ? getInitials(nurse.name) : 'UN';
  };

  const navigateDate = (direction: 'prev' | 'next') => {
    const newDate = new Date(currentDate);
    newDate.setDate(currentDate.getDate() + (direction === 'next' ? 1 : -1));
    setCurrentDate(newDate);
  };

  const todayVisits = visits?.filter(visit => {
    const visitDate = new Date(visit.date);
    return visitDate.toDateString() === currentDate.toDateString();
  }) || [];

  if (visitsLoading) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <h1 className="text-2xl font-bold text-gray-900">Schedule</h1>
        </div>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {[1, 2, 3].map((i) => (
            <Card key={i}>
              <CardContent className="p-6">
                <div className="animate-pulse">
                  <div className="h-4 bg-gray-200 rounded w-3/4 mb-2"></div>
                  <div className="h-8 bg-gray-200 rounded w-1/2"></div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h1 className="text-2xl font-bold text-gray-900">Schedule</h1>
        <Button variant="primary" size="lg" className="shadow-lg">
          <Plus className="h-5 w-5 mr-2" />
          New Visit
        </Button>
      </div>

      {/* Date Navigation */}
      <Card>
        <CardContent className="p-6">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <Button
                variant="outline"
                size="sm"
                onClick={() => navigateDate('prev')}
              >
                <ChevronLeft className="h-4 w-4" />
              </Button>
              <h2 className="text-xl font-semibold text-gray-900">
                {currentDate.toLocaleDateString('en', { 
                  weekday: 'long', 
                  year: 'numeric', 
                  month: 'long', 
                  day: 'numeric' 
                })}
              </h2>
              <Button
                variant="outline"
                size="sm"
                onClick={() => navigateDate('next')}
              >
                <ChevronRight className="h-4 w-4" />
              </Button>
            </div>
            <Button
              variant="outline"
              size="sm"
              onClick={() => setCurrentDate(new Date())}
            >
              Today
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-blue-100 rounded-lg">
                <Calendar className="h-6 w-6 text-blue-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Today's Visits</p>
                <p className="text-2xl font-bold text-gray-900">{todayVisits.length}</p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-green-100 rounded-lg">
                <Clock className="h-6 w-6 text-green-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Completed</p>
                <p className="text-2xl font-bold text-gray-900">
                  {todayVisits.filter(v => v.status === 'completed').length}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card>
          <CardContent className="p-6">
            <div className="flex items-center">
              <div className="p-2 bg-yellow-100 rounded-lg">
                <User className="h-6 w-6 text-yellow-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm font-medium text-gray-600">Scheduled</p>
                <p className="text-2xl font-bold text-gray-900">
                  {todayVisits.filter(v => v.status === 'scheduled').length}
                </p>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Today's Visits */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center">
            <Calendar className="h-5 w-5 mr-2" />
            Today's Visits
          </CardTitle>
          <CardDescription>
            Visits scheduled for {currentDate.toLocaleDateString('en', { 
              weekday: 'long', 
              month: 'long', 
              day: 'numeric' 
            })}
          </CardDescription>
        </CardHeader>
        <CardContent>
          {todayVisits.length === 0 ? (
            <div className="text-center py-12 text-gray-500">
              <Calendar className="h-16 w-16 mx-auto mb-4 text-gray-300" />
              <h3 className="text-xl font-semibold text-gray-900 mb-2">No visits scheduled</h3>
              <p className="text-gray-500 mb-6">No visits are scheduled for this day.</p>
              <Button>
                <Plus className="h-4 w-4 mr-2" />
                Schedule First Visit
              </Button>
            </div>
          ) : (
            <div className="space-y-4">
              {todayVisits.map((visit) => (
                <div 
                  key={visit.id} 
                  className="flex items-center justify-between p-6 bg-white border border-gray-200 rounded-lg hover:shadow-md transition-shadow"
                >
                  <div className="flex items-center space-x-4">
                    <div className="h-12 w-12 bg-blue-100 rounded-full flex items-center justify-center">
                      <span className="text-lg font-semibold text-blue-700">
                        {getNurseInitials(visit.nurseId)}
                      </span>
                    </div>
                    <div className="flex-1">
                      <h4 className="text-lg font-semibold text-gray-900">
                        {getPatientName(visit.patientId)}
                      </h4>
                      <p className="text-sm text-gray-600 mb-1">
                        {getNurseName(visit.nurseId)}
                      </p>
                      <div className="flex items-center space-x-4 text-sm text-gray-500">
                        <div className="flex items-center">
                          <Clock className="h-4 w-4 mr-1" />
                          {formatTime(visit.windowStart)} - {formatTime(visit.windowEnd)}
                        </div>
                        {visit.location?.address && (
                          <div className="flex items-center">
                            <MapPin className="h-4 w-4 mr-1" />
                            {visit.location.address}
                          </div>
                        )}
                      </div>
                    </div>
                  </div>
                  <div className="flex items-center space-x-3">
                    <Badge variant="outline" className="text-sm">
                      {visit.status.replace('_', ' ')}
                    </Badge>
                    <div className="flex space-x-1">
                      <Button variant="ghost" size="sm" onClick={() => setSelectedVisit(visit)}>
                        <Eye className="h-4 w-4" />
                      </Button>
                      <Button variant="ghost" size="sm">
                        <Edit className="h-4 w-4" />
                      </Button>
                      <Button variant="ghost" size="sm">
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </CardContent>
      </Card>

      {/* Visit Detail Modal */}
      {selectedVisit && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
          <div className="bg-white rounded-lg max-w-md w-full p-6">
            <div className="flex items-center justify-between mb-4">
              <h3 className="text-lg font-semibold text-gray-900">Visit Details</h3>
              <Button 
                variant="ghost" 
                size="sm" 
                onClick={() => setSelectedVisit(null)}
                className="h-8 w-8 p-0"
              >
                
              </Button>
            </div>
            
            <div className="space-y-4">
              <div>
                <label className="text-sm font-medium text-gray-500">Patient</label>
                <p className="text-gray-900">{getPatientName(selectedVisit.patientId)}</p>
              </div>
              
              <div>
                <label className="text-sm font-medium text-gray-500">Nurse</label>
                <p className="text-gray-900">{getNurseName(selectedVisit.nurseId)}</p>
              </div>
              
              <div>
                <label className="text-sm font-medium text-gray-500">Time</label>
                <p className="text-gray-900">
                  {formatTime(selectedVisit.windowStart)} - {formatTime(selectedVisit.windowEnd)}
                </p>
              </div>
              
              <div>
                <label className="text-sm font-medium text-gray-500">Status</label>
                <p className="text-gray-900">{selectedVisit.status}</p>
              </div>
              
              {selectedVisit.location?.address && (
                <div>
                  <label className="text-sm font-medium text-gray-500">Location</label>
                  <p className="text-gray-900">{selectedVisit.location.address}</p>
                </div>
              )}
            </div>
            
            <div className="flex justify-end space-x-2 mt-6">
              <Button variant="outline" size="sm">
                <Edit className="h-4 w-4 mr-1" />
                Edit
              </Button>
              <Button variant="outline" size="sm">
                <Eye className="h-4 w-4 mr-1" />
                View
              </Button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}